  .mobile-menu-inner {
    display: block;
    width: 100%;
    text-align: center;
    background: #000;
    position: fixed;
    z-index: 9999;
    left: -100%;
    -webkit-transition: all 0.7s cubic-bezier(0.65, -0.22, 0.35, 1.13);
    transition: all 0.7s cubic-bezier(0.65, -0.22, 0.35, 1.13);
    height: 100vh;
    overflow-x: hidden;
    overflow-y: scroll;
    top: 0;
  }
  
  .mobile-menu-inner.mnav-fixed {
