  .accordion-menu {
    width: 100%;
    margin: 20px auto 50px;
    text-align: left;
    border-radius: 4px;
    list-style: none;
  }
  
  .accordion-menu ul {
    list-style: none;
  }
  
  .accordion-menu > li {
    position: relative;
    border-top: 1px solid rgba(255, 255, 255, 0.3);
  }
  
  .accordion-menu > li:first-child {
    border-top: 0;
  }
  
  .accordion-menu > li.single-link {
    margin: 3px 0;
  }
  
  .accordion-menu > li .dropdownlink:before {
    content: "\f105";
    position: absolute;
    right: 0;
    top: 8px;
    font: normal normal normal 14px/1 FontAwesome;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    font-size: 16px;
    height: 30px;
    width: 30px;
    background: rgba(255, 255, 255, 0.2);
    text-align: center;
    line-height: 29px;
  }
  
  .accordion-menu > li.open > a {
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
  }
  
  .accordion-menu > li.open .dropdownlink:before {
    content: "\f107";
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }
  
  .accordion-menu > li > a {
    font-weight: 500;
    font-size: 18px;
    text-decoration: none;
    text-transform: uppercase;
    padding: 10px 0;
    color: #fff;
    display: block;
    font-family: "Changa", sans-serif;
  }
  
  .accordion-menu .accordion-menu li.open .dropdownlink {
    color: #e43a90;
  }
  
  .accordion-menu .accordion-menu li:last-child .dropdownlink {
    border-bottom: 0;
  }
  
  .accordion-menu .accordion-menu .fa-angle-down {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
  
  .accordion-menu .dropdownlink {
    cursor: pointer;
    color: #fff;
    position: relative;
    -webkit-transition: all 0.4s ease-out;
    transition: all 0.4s ease-out;
  }
  
  .accordion-menu .dropdownlink i {
    position: absolute;
    top: 13px;
    left: 16px;
    font-size: 14px;
  }
  
  .accordion-menu .dropdownlink .fa-angle-down {
    right: 12px;
    left: auto;
  }
  
  .accordion-menu .dropdownlink:last-child {
    border-bottom: none;
  }
  
  .accordion-menu .submenuItems {
    display: none;
    background: transparent;
  }
  
  .accordion-menu .submenuItems li:last-child {
    border-bottom: none;
  }
  
  
  
  .accordion-menu .submenuItems:hover, .accordion-menu .submenuItems:focus {
    color: #e43a90;
  }
  
