/* TYPOGRAPHY */
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Changa", sans-serif;
}

.section-hero {
  width: 100%;
  padding: 120px 0;
}

.hero-bg {
  background-color: #f60038;
  color: #ffffff;
}

.black-hero-bg {
  background-color: #0e0e0e;
  color: #ffffff;
}

.about-box {
  display: flex;
  align-items: center
}

.section-title {
  margin-bottom: 50px;
}

.section-title h2 {
  font-size: 35px;
  line-height: 26px;
  color: #444;
  font-weight: 900;
  text-transform: uppercase;
  margin-bottom: 23px;
  padding-bottom: 25px;
  position: relative;
  display: inline-block;
}

.section-title h2:before,
.section-title h2:after {
  position: absolute;
  content: '';
  bottom: 0;
  background: #f16282;
  left: 50%;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
}

.section-title h2:before {
  width: 200px;
  height: 1px;
}

.section-title h2:after {
  height: 3px;
  bottom: -1px;
  width: 30px;
  left: 50%;
}

.section-title h2 span {
  color: #f16282;
}

.section-title p {
  color: #494848;
  font-size: 16px;
  margin: 0;
}

.section-title.title-two h2 {
  margin-bottom: 30px;
}

.section-title.title-two h2:before {
  display: none;
}

.section-title.title-two h2:after {
  background-image: url("https://themeim.com/demo/milando/media/background/2.png");
  background: transparent;
  height: 25px;
  max-width: 320px;
  background-repeat: no-repeat;
  width: 100%;
  left: 50%;
  bottom: -20px;
}

.section-title.title-three h2:before {
  background-image: url(data:image/png;base64,);
  background-color: transparent;
  height: 15px;
  width: 150px;
  background-repeat: no-repeat;
  background-size: contain;
  bottom: -8px;
}

.section-title.title-three h2:after {
  display: none;
}

.section-title.style-four {
  width: 100%;
  text-align: center;
}

.section-title.style-four h2 {
  color: #f60038;
  font-weight: 700;
}

.section-title.style-four h2::before {
  width: 70px;
  height: 1px;
  background: #f60038;
}

.section-title.style-four h2::after {
  background: #f60038;
}

.section-title.style-four p {
  padding: 0px 28%;
  font-size: 18px;
}

.section-title.style-five {
  text-align: left;
  margin-bottom: 90px;
}

.section-title.style-five h2::before {
  width: 70px;
  height: 1px;
  left: 0;
  -webkit-transform: translate(0);
  transform: translate(0);
}

.section-title.style-five h2::after {
  background: #f60038;
  left: 0;
  -webkit-transform: translate(0);
  transform: translate(0);
}

.section-title.style-five p {
  padding: 0;
  width: 350px;
  font-weight: 300;
  font-size: 18px;
}

.section-title.style-five h2 {
  color: #f60038;
  font-weight: 700;
}

.section-title.style-six {
  text-align: left;
  margin-bottom: 12px;
}

.section-title.style-six p {
  width: 350px;
  font-weight: 300;
}

.section-title.style-six h2::before {
  width: 70px;
  height: 1px;
  left: 0;
  -webkit-transform: translate(0);
  transform: translate(0);
}

.section-title.style-six h2::after {
  background: #f60038;
  left: 0;
  -webkit-transform: translate(0);
  transform: translate(0);
}

.section-title.style-six p {
  padding: 0;
  width: 350px;
}

.section-title.style-six h2 {
  color: #f60038;
  font-weight: 700;
}

.section-dark .section-title h2 {
  color: #fff;
}

.section-dark .section-title p {
  color: #fff;
}

.section-dark .section-title.title-three h2:before {
  background-image: url(data:image/png;base64,);
}

/* Button */
.tim-btn {
  padding: 10px 41px;
  border: 1px solid #e4e4e4;
  color: #363636;
  display: inline-block;
  font-size: 20px;
  font-weight: 500;
  font-family: "Changa", sans-serif;
  border-radius: 5px;
}

.tim-btn:hover,
.tim-btn:focus {
  color: #f60038 !important;
  opacity: 0.9;
}

.tim-btn.tim-btn-bgt {
  border: 1px solid #e4e4e4;
  color: #363636;
}

.tim-btn.tim-btn-bgt:hover {
  color: #fff;
}

.video-btn-two {
  padding: 13px 37px;
  border: 1px solid #fff;
  color: #fff;
  border-radius: 4px;
  vertical-align: middle;
  display: inline-block;
  text-transform: uppercase;
  font-weight: 600;
  font-family: "Changa", sans-serif;
}

.video-btn-two i {
  margin-right: 20px;
  color: #e43a90;
  vertical-align: middle;
  font-size: 24px;
  position: relative;
}

.video-btn-two i:after {
  position: absolute;
  content: '';
  height: 20px;
  width: 1px;
  background: #7c7d81;
  right: -14px;
  top: 2px;
}

.video-btn-two:hover {
  background: #e43a90;
  border-color: #e43a90;
  color: #fff;
}

.video-btn-two:hover i {
  color: #fff;
}

.video-btn-two:hover i:after {
  background: #fff;
}

.store-btn {
  padding: 10px 25px;
  color: #fff;
  border: 2px solid #fff;
  font-weight: 600;
  border-radius: 4px;
  font-size: 18px;
  vertical-align: top;
  display: inline-block;
}

.store-btn i {
  font-size: 25px;
  margin-right: 7px;
}

.store-btn:hover {
  color: #fff;
  background: #e43a90;
  border-color: #e43a90;
}

.play-btn-round {
  width: 39px;
  height: 39px;
  background: #f7dfe2;
  text-align: center;
  border-radius: 50px;
  line-height: 39px;
  color: #e01e36;
  display: inline-block;
}

