/*--------------- Banner five text animation ------------*/
.sp-container {
  position: fixed;
  top: -135px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: 0;
}

.sp-content {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0px;
  top: 0px;
  z-index: 1000;
}

