  overflow: hidden;
  max-width: 400px;
  margin: 0 auto 30px;
}

.performace:before {
  position: absolute;
  content: '';
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: rgba(228, 58, 144, 0.58);
  opacity: 0;
