.color-checkbox {
  width: 20px;
  height: 20px;
  box-sizing: border-box;
  margin-right: 7px;
  position: relative;
  border-radius: 16px;
  -webkit-transition: -webkit-transform 0.1s linear;
  transition: -webkit-transform 0.1s linear;
  transition: transform 0.1s linear;
  transition: transform 0.1s linear, -webkit-transform 0.1s linear;
  cursor: pointer;
}

.color-checkbox.bordered {
  box-shadow: 0 0 0 2px #f0f0f0 inset;
}

.color-checkbox.bordered:after {
  border-color: black;
}

.color-checkbox:after {
  content: '';
  display: block;
  width: 6px;
  height: 10px;
  position: absolute;
  top: 3px;
  left: 7px;
  border: 2px solid white;
  border-width: 0 2px 2px 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  opacity: 0;
  -webkit-transition: opacity 0.1s;
  transition: opacity 0.1s;
  text-align: center;
}

.color-checkbox:hover,
.color-checkbox__input:focus+.color-checkbox {
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
}

.color-checkbox__input:checked+.color-checkbox:after {
  opacity: 1;
}

.color-checkbox__input {
  visibility: hidden;
  width: 0;
  pointer-events: none;
  position: absolute;
}

