/*--------------------------------------------------------------
  ## Albums
  --------------------------------------------------------------*/
.artist-thump .detail {
  font-size: 14px;
}

.album-thumb {
  height: 295px;
  width: 295px;
  position: relative;
  border-radius: 4px;
  overflow: hidden;
}

.album-thumb:after {
  background-color: rgba(0, 0, 0, 0.33);
  bottom: 0;
  content: "";
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
}

.album-thumb .play-btn-round {
  position: absolute;
  bottom: 14px;
  left: 14px;
  z-index: 1;
}

.artist-thump .product-price {
  display: none;
}

.artist-thump .product-thumb-hover {
  display: none;
}

.artist-thump .detail .play-btn-round {
  position: absolute;
  bottom: 131px;
  left: 37px;
  width: 41px;
  height: 41px;
  background: #f7dfe2;
  text-align: center;
  line-height: 42px;
}

.album-player .play-btn-large {
  color: #fff;
  background: #f60038;
  border-color: #f60038;
}

.player-top-btn .tim-btn {
  padding: 3px 11px;
  font-size: 15px;
  font-weight: 500;
  margin-right: 0.6em;
}

.single-album-player {
  margin-top: 1em;
}

.cell-play-icons {
  display: block;
  height: 28px;
  position: relative;
  width: 28px;
}

.cell-play-icons:after {
  background-color: #0a0a16;
  border-radius: 50%;
  bottom: 0;
  content: "";
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
}

.cell-play-icons .fa-play {
  color: #fff;
  left: 50%;
  position: absolute;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  z-index: 1;
  font-size: 10px;
}

.single-album-player .audio__player {
  z-index: 9;
}

.single-album-player .progress {
  float: none;
}

.related-album-single.bottom {
  margin-top: 5em;
}

.artist-timeline {
  display: grid;
  grid-gap: 4.75em;
  grid-template-areas:
    ". entry1"
    "entry2 ."
}

/*--------------------------------------------------------------
  ## Vlog
  --------------------------------------------------------------*/
.list-blog {
  padding: 20px;
}

.list-index {
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  position: relative;
}

.progression-podcast-index-content {
  padding: 50px 35px 50px 35px;
  position: relative;
  z-index: 10;
}

.progression-studios-podcast-index-border-top {
  background: rgba(255, 255, 255, 0.25);
  height: 10px;
  border-radius: 30px;
  margin-bottom: 15px;
}

