  ## Cart
  --------------------------------------------------------------*/
.cart-item {
  padding: 30px 0;
  border-bottom: 1px solid #e6e6e6;
  display: flex;
}
.cart-image {
  width: 230px;
  height: 230px;
  margin-right: 45px;
  background-color: #fff;
  display: flex;
  position: relative;
}
