  width: 100%;
  height: 100%;
  object-fit: cover;
} */
.cart-description {
  display: flex;
  align-items: center;
  padding-top: 15px;
  padding-right: 20px;
  width: 100%;
}
.cart-description h2 {
  font-size: 25px;
  line-height: 1.4;
  width: 100%;
}
.short-desc{
  display: block;
  color: #878787;
  font-size: 15px;
  height: 20px;
  font-weight: 500;
