  font-style: normal;
  font-weight: 500;
  margin-right: 0.44em;
}
.product-price span {
  font-size: 21px;
  line-height: 1.4px;
  font-weight: 500;
}
.delet-line .remove{
  font-size:27px;
}
.cart-total {
  border-top: 1px solid #f0f0f0;
  background: #fff;
  padding: 16px 22px;
  box-shadow: 0 -2px 10px 0 rgba(0,0,0,.1);
}
.goback a{
  font-size:17px;
  color: #f60038;
  font-weight: 500;
}
.goback .ico{
    margin-right: 0.5em;
}
